import React from "react";
import { HotelPostGridLoader } from "components/ContentLoader/ContentLoader";
import SectionGrid from "components/global-components/Components/SectionGrid/SectionGrid.cra";
import size from "lodash/size";
import slice from "lodash/slice";
import { useEffect, useState } from "react";
import { connect } from "react-redux";
import { selectList } from "state/Listing/Selectors";

const ListingTable = ({
  getIndex,
  onEditItem,
  list,
  isLoading,
  onFavourite,
  update,
  ...rest
}) => {
  const [limit, setLimit] = useState(5);

  useEffect(() => {
    getIndex();
  }, []);

  let columnWidth = [1 / 2, 1 / 2, 1 / 3, 1 / 4, 1 / 4, 1 / 4, 1 / 4];

  const handleFavourite = async (id, params) => await update(id, params);

  list = slice(list, 0, 8);
  return (
    <SectionGrid
      link={"/singleproperty"}
      onFavourite={handleFavourite}
      columnWidth={columnWidth}
      data={list}
      onEdit={onEditItem}
      totalItem={size(list)}
      loading={isLoading}
      limit={limit}
      handleLoadMore={() => setLimit(limit + 2)}
      placeholder={<HotelPostGridLoader />}
    />
  );
};

const mapStateToProps = (state) => {
  return {
    list: selectList(state),
  };
};

const mapDispatchToProps = (dispatch) => ({
  // getList: (params) => dispatch(getList(params)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ListingTable);
