import React from 'react';
import { AuthContext } from "Boot/auth/AuthProvider";
import { PROPERTY } from "components/Routes/Constants";
import isNil from "lodash/isNil";
import { useContext } from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import { withRouter } from "react-router-dom";
import Favourite from "../Favorite/Favorite";
import GridCard from "../GridCard/GridCard";
import TextLink from "../TextLink/TextLink";

const responsive = {
  desktop: {
    breakpoint: {
      max: 3000,
      min: 1024,
    },
    items: 1,
    paritialVisibilityGutter: 40,
  },
  mobile: {
    breakpoint: {
      max: 464,
      min: 0,
    },
    items: 1,
    paritialVisibilityGutter: 30,
  },
  tablet: {
    breakpoint: {
      max: 1024,
      min: 464,
    },
    items: 1,
    paritialVisibilityGutter: 30,
  },
};

const PostGrid = ({
  tenancy_details: { minimum_tenancy_length = "", monthly_rent = "" },
  amenities,
  property_address,
  advert_type,
  unit_number,
  property_type,
  number_of_beds,
  number_of_bathrooms,
  furnishing_options,
  property_description,
  availability_for_viewing_note,
  availability_for_remote_video_viewing,
  display_id,
  youtube_uri,
  is_favourite = false,
  onEdit,
  link,
  history,
  created_by,
  onFavourite,
  image_attributes,
  ...props
}) => {
  let imagealt = "Loading...";
  const { user } = useContext(AuthContext);

  const handleOnClick = () => {
    onEdit({ _id: display_id }, ({ data }) => {
      if (!isNil(user) && user._id === created_by) {
        history.push(`${PROPERTY}/${display_id}#step1`);
      } else {
        history.push(`/listing/${display_id}`);
      }
    });
  };

  return (
    <GridCard
      isCarousel={true}
      location={property_address}
      beds={number_of_beds}
      baths={number_of_bathrooms}
      propertyType={property_type}
      furnished={furnishing_options}
      title={`$${monthly_rent}/month+ ${number_of_beds} bds `}
      favorite={<Favourite active={is_favourite} onClick={(event) => onFavourite(display_id, {is_favourite: !is_favourite} )} />}
      // location={`${property_type}   ${unit_number}`}
      // title={
      //   <TextLink
      //     onClick={handleOnClick}
      //     link={`${link}`}
      //     content={property_address}
      //   />
      // }
      // price={`$${monthly_rent}/Month- Max Tenancy: ${minimum_tenancy_length} `}
      // rating={<Rating rating={rating} ratingCount={ratingCount} type="bulk" />}
      viewDetailsBtn={
        <TextLink onClick={handleOnClick} content="View Details" />
      }
    >
      <Carousel
        additionalTransfrom={0}
        arrows
        autoPlaySpeed={100}
        focusOnSelect={false}
        infinite
        responsive={responsive}
        showDots={false}
        slidesToSlide={1}
      >
        {image_attributes.map((item, index) => (
          <img
            onClick={handleOnClick}
            key={index}
            alt={imagealt}
            src={item.location}
            className="h-100 w-100"
          />
        ))}
      </Carousel>
    </GridCard>
  );
};

export default withRouter(PostGrid);
